<template>
    <div data-v-335e170c="" class="page">
        <div data-v-152ab910="" data-v-335e170c="" class="sub-header">
            <div data-v-152ab910="" class="back" @click="$router.go(-1)"></div>
            <div data-v-152ab910="" class="title">个人资料</div>
            <div data-v-152ab910="" class="right-btn" @click="onUpdataUser">保存</div>
        </div>
        <div data-v-335e170c="" class="main">
            <ul data-v-335e170c="" class="head">
                <li data-v-335e170c="">头像</li>
                <li data-v-335e170c=""><img data-v-335e170c="" :src="showHead"
                                            alt=""><input data-v-335e170c="" @change="uploadHeads" type="file"></li>
            </ul>
            <ul data-v-335e170c="" class="list">
                <li data-v-335e170c="">昵称</li>
                <li data-v-335e170c=""><input data-v-335e170c="" v-model="nickname"  type="text" placeholder="请设置昵称"></li>
                <li data-v-335e170c="" class="arrow"></li>
            </ul>
            <ul data-v-335e170c="" class="list">
                <li data-v-335e170c="">性别</li>
                <li data-v-335e170c="">
                    <input data-v-335e170c="" @click="isGender=true" :value="showGendres" type="text" placeholder="请选择性别" readonly="readonly">
                </li>
                <li data-v-335e170c="" class="arrow"></li>
            </ul>
        </div>
        <van-action-sheet
                v-model="isGender"
                :actions="genders"
                cancel-text="取消"
                title="请选择性别"
                @select="selectGender"
        />
        <!---->
    </div>
</template>

<script>
    import Vue from 'vue';
    import { ActionSheet } from 'vant';
    import {mapActions} from  "vuex"
    Vue.use(ActionSheet);
    export default {
        data(){
            return {
                isGender:false,
                genders:[
                    {name:"男"},
                    {name:"女"}
                ],
                showGendres:"",
                gender:"",
                showHead:require("../../../assets/images/user/my/default-head.png"),
                head:"",
                nickname:''
            }
        },
        mounted(){
            document.title=this.$route.meta.title;
        },
        created(){
            //安全认证
            this.$utils.safeUser(this);
            //控制 反正多次请求
            this.onSumit =true;
            //获取会员信息
            this.getUserInfo({success:(res)=>{
                    console.log(res)
                    if (res.code ===200){
                        this.nickname = res.data.nickname;
                        this.gender=res.data.gender;
                        this.showGendres=this.gender==="1"?"男":this.gender==='2'?"女":"";
                        this.showHead=res.data.head?res.data.head:require("../../../assets/images/user/my/default-head.png");
                    }
                }})
        },
        methods:{
            ...mapActions({
                //头像shang
                uploadHead:"user/uploadHead",
                //修改会员信息
                getUpdateUser:"user/getUpdateUser",
                //获取会员信息
                getUserInfo:"user/getUserInfo"
            }),
            //性别选择
            selectGender(val){
                this.showGendres = val.name;
                this.gender = this.showGendres==='男'?'1':this.showGendres==='女'?'2':'';
                console.log(  this.gender)
            },
            //头像上传
            uploadHeads(e){
                this.uploadHead({headfile:e.target.files[0],success:(res)=>{
                        if(res.code===200){
                            this.showHead="http://vueshop.glbuys.com/userfiles/head/"+res.data.msbox;
                            this.head=res.data.msbox;
                        }
                    }})
            },
            //修改会员信息
            onUpdataUser(){
                if (this.onSumit){
                    this.onSumit =false;
                    this.getUpdateUser({
                        nickname:this.nickname,
                        gender:this.gender,
                        head:this.head,
                        success:(res)=>{
                            if (res.code === 200){
                                this.$router.go(-1)
                            }
                        }
                    })
                }

            }
        }
    }
</script>

<style scoped>
    .sub-header {
        width: 100%;
        height: 1rem;
        background-color: #FFFFFF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        border-bottom: 1px solid #EFEFEF;
        position: fixed;
        z-index: 10;
        left: 0;
        top: 0;
        justify-content: space-between;
        padding: 0px 0.1rem;
        box-sizing: border-box
    }

    .sub-header .back {
        width: 0.8rem;
        height: 0.8rem;
        background: url("../../../assets/images/home/goods/back.png") no-repeat center;
        background-size: 100%;

    }

    .sub-header .title {
        width: 79%;
        height: auto;
        font-size: 0.32rem;
        text-align: center;
        position: absolute;
        z-index: 1;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%)
    }

    .sub-header .right-btn {
        width: auto;
        height: auto;
        font-size: 0.32rem;
    }

    .page {
        width: 100%;
        height: 100vh;
        background-color: #FFFFFF;
        overflow: hidden;
    }

    .main {
        width: 100%;
        margin-top: 1.02rem;
    }

    .main ul.head {
        width: 100%;
        height: 1.2rem;
        border-bottom: 1px solid #EFEFEF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        justify-content: space-between;
        -webkit-justify-content: space-between;
    }

    .main ul.head li:nth-child(1) {
        font-size: 0.28rem;
        margin-left: 5%;
    }

    .main ul.head li:nth-child(2) {
        width: 1rem;
        height: 1rem;
        margin-right: 10%;
        position: relative;
        z-index: 1;
    }

    .main ul.head li:nth-child(2) img {
        width: 100%;
        height: 100%;
        border-radius: 100%;
    }

    .main ul.head li:nth-child(2) input {
        width: 100%;
        height: 95%;
        position: absolute;
        z-index: 1;
        left: 0;
        top: 0;
        opacity: 0;
    }

    .main ul.list {
        width: 100%;
        height: 0.8rem;
        border-bottom: 1px solid #EFEFEF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        font-size: 0.28rem;
    }

    .main ul.list li:nth-child(1) {
        margin-left: 5%;
    }

    .main ul.list li:nth-child(2) {
        width: 50%;
        height: 100%;
        margin-left: 20%;
    }

    .main ul.list li:nth-child(2) input {
        width: 100%;
        height: 95%;
        text-align: right;
        font-size: 0.28rem;
    }

    .main ul.list li.arrow {
        width: 0.4rem;
        height: 0.4rem;
        background-image: url("../../../assets/images/common/right_arrow.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
        margin-right: 3%;
    }
</style>
